.contacts__wrapper {
  margin: 0 auto;
  min-width: $mobile-width;
}

.contacts__advert {
  display: flex;
  justify-content: space-between;
  padding: 30px $mobile-padding;

  &--gray {
    background-color: $gray;
  }
}

.contacts__text {
  width: 135px;

  &--address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  line-height: 1.43;
  color: $brown;
  }

  &--bold {
    @include title-small(16px, 1.25, $darkest);
  }
}

.contacts__map {
  position: relative;

  margin: 0 auto;
  min-width: $mobile-width;
  height: 400px;

  background: url("../img/bg-map-mobile@1x.png") no-repeat #f9f7f1;
  background-position: center;

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
  background-image: url("../img/bg-map-mobile@2x.png");
  background-size: 320px 400px;
  }

  &::before {
    content: "";

    position: absolute;
    top: 50%;
    left: 50%;

    width: 55px;
    height: 53px;
    margin-left: -25px;
    margin-top: -25px;

    background: url("../img/map-pin.png") no-repeat;
    background-size: 55px 53px;
  }
}

.contacts__frame {
  width: 100%;
  height: 400px;
}

@media (min-width: $tablet-width) {
  .contacts__advert {
    padding: 70px 55px 60px 70px;
  }

  .contacts__text {
    width: 155px;
    &--bold {
      font-size: 20px;
      line-height: 1.25
    }

    &--address {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      font-size: 16px;
      line-height: 1.25;
      text-align: right;
    }
  }

  .contacts__map {
    margin: 0 auto;
    width: 100%;

    background: url("../img/bg-map-tablet@1x.png") no-repeat #f9f7f1;
    background-position: center;

    @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-tablet@2x.png");
    background-size: 767px 400px;
  }
    &::before {
      width: 113px;
      height: 106px;
      margin-left: -45px;
      margin-top: -50px;

      background-size: 113px 106px;
    }
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    position: relative;
    width: $desktop-width;
  }

  .contacts__advert {
    position: absolute;
    top: 120px;
    left: 110px;
    z-index: 5;

    padding: 60px 75px;
    width: 570px;

    background-color: #ffffff;
  }

  .contacts__map {
    background: url("../img/bg-map-desktop@1x.png") no-repeat #f9f7f1;
    background-position: center;

    @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-desktop@2x.png");
    background-size: 767px 400px;
  }
  }
}
