.about__wrapper {
 margin: 0 auto;
 padding: 0 $mobile-padding;
 min-width: $mobile-width;
}

.about__list{
  @include list-reducer;
}

.about__item {
  position: relative;

  display: flex;
  justify-content: flex-start;
  align-content: center;
  margin-bottom: 20px;
  padding-left: 80px;
  min-height: 60px;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 60px;
    height: 60px;

    background-color: $green;
    background-image: url("../img/sprite-png-1x.png");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    background-image: url("../img/sprite-png-2x.png");
    background-size: 260px 50px;
    }
  }

  &:first-of-type::before {
    background-position: -133px 5px;
  }

  &:nth-of-type(2)::before{
    background-position: -204px 5px;
  }

  &:nth-of-type(3)::before{
    background-position: -67px 5px;
  }

  &:nth-of-type(4)::before{
    background-position: 5px 5px;
  }

}

.about__text {
  margin: 6px 0;
  line-height: 1.29;
}

@media (min-width: $tablet-width) {
  .about__wrapper {
    padding: 0 $tablet-padding;

    border-bottom: 1px solid $border-color;
  }

  .about__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 50px;
  }

  .about__item {
    margin: 20px 60px 60px 0;
    padding: 100px 0 45px 0;
    width: 245px;

    &::before {
      width: 80px;
      height: 80px;
    }

    &:first-of-type::before {
      background-position: -130px 12px;
    }

    &:nth-of-type(2)::before{
      background-position: -205px 12px;
    }

    &:nth-of-type(3)::before{
      background-position: -57px 12px;
    }

    &:nth-of-type(4)::before{
      background-position: 15px 15px;
    }

    @for $i from 1 through 4 {
      &--#{$i}::after {
        content: "#{$i}";

        position: absolute;
        top: 0;
        right: 0;
        z-index: -2;

        font-family: "Oswald-Regular", "Arial", sans-serif;
        font-size: 281px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 0.74;
        color: #f2f2f2;
      }
    }
  }

  .about__text {
    margin: 15px 0 0;

    font-size: 16px;
    line-height: 1.5;
  }
}

@media (min-width: $desktop-width) {
  .about__wrapper {
    @include desktop-wrapper;
    border: none;
  }
}
