@media (min-width: $desktop-width) {
  .header{
    &::before {
      content: "";

      position: absolute;
      top: 0;
      right: 0;
      z-index: -3;

      width: 50%;
      min-height: 696px;

      background: url("../img/index-background-desktop@1x.jpg") left no-repeat $deep-green;
      background-size: 720px 696px;

      @media (min-resolution: $retina-dpi),
        (min-resolution: $retina-dppx) {
          background: url("../img/index-background-desktop@2x.jpg") left no-repeat $deep-green;
          background-size: 720px 696px;
        }
    }
  }
}
